import EiCalendarItem from './ei-calendar-item';
import EDate from './EDate';
import moment from '@/components/moment';
let CustomDate = {};

export default {
	name: 'ei-calendar',
	components: { EiCalendarItem },
	props: {
		visible: {
			type: Boolean
		},
		value: {
			type: [Array, String]
		},
		type: {
			validator(value) {
				return ['range', 'multiple', 'single'].indexOf(value) !== -1;
			},
			default: 'single'
		},
		disabled: {
			type: Boolean
		},
		cellClass: {
			type: Object,
			default: () => ({})
		},
		disabledDate: {
			type: [Array, Function],
			default: () => []
		},
		customDate: {
			type: [Array, Function],
			default: () => []
		},
		format: String,
		title: {
			type: String,
			default: '请选择日期'
		},
		drawer: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			visibleSync: false,
			active: false,
			closeTimer: null,
			watchTimer: null,
			week: ['日', '一', '二', '三', '四', '五', '六'],
			months: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
			selectedValue: undefined,
			selection: [],
			currentDate: undefined,
			showMonthList: [],
			currentIndex: 1,
			duration: 0
		};
	},
	computed: {
		currentDateShow() {
			return this.currentDate ? `${this.currentDate.replace('/', '年')}月` : '';
		}
	},
	watch: {
		visible(val) {
			clearTimeout(this.watchTimer);
			setTimeout(() => {
				this.active = val;
			}, 100);
			if (this.visibleSync) {
				clearTimeout(this.closeTimer);
			}
			if (val) {
				this.visibleSync = val;
				this.initValue();
			} else {
				this.watchTimer = setTimeout(() => {
					this.showMonthList = [];
					// console.log(this.showMonthList );
					this.currentIndex = 1;
					this.visibleSync = val;
				}, 300);
			}
		}
	},
	created() {
		this.initVisible();
		// console.log(this.showMonthList)
	},
	methods: {
		initVisible() {
			this.visibleSync = this.visible;
			if (this.visible) {
				this.initValue();
			}
			setTimeout(() => {
				this.active = this.visible;
			}, 100);
		},
		 initCustomData() {
      if (Array.isArray(this.customDate)) {
        CustomDate = {};
        this.customDate.forEach((item) => {
          const date = typeof item === 'string' ? item : item.date;
          if (date) {
            const format = new EDate([date]).format('YYYY/MM/DD');
            CustomDate[format] = typeof item === 'string' ? {
              date,
              top: [{}]
            } :item;
          }
        });
      }
    },
		initValue() {
			this.initCustomData();
			if (this.type === 'single') {
				const selectedValue = new EDate([this.value], 'YYYY/MM/DD');
				this.selectedValue = selectedValue.getTime();
				this.currentDate = selectedValue.format('YYYY/MM');
			} else {
				const valueList = this.value || [];
				const selection = [];
				valueList.forEach(item => {
					selection.push(new EDate([item], 'YYYY/MM/DD').getTime());
				});
				this.selection = selection.sort();
				this.currentDate = EDate.format(this.selection[0], 'YYYY/MM') || new EDate().format('YYYY/MM');
			}
			// console.log('initValue');
			this.setShowMonthList(1);
		},
		setShowMonthList(index, refresh) {
			if (!this.currentDate) return;
			const currentDate = this.currentDate;
			const beforeDate = EDate.modify(`${this.currentDate}/01`, { m: -1 }).format('YYYY/MM'); // ios 预览下 new Date('2019/02')返回null
			const afterDate = EDate.modify(`${this.currentDate}/01`, { m: +1 }).format('YYYY/MM');
			console.log(!this.showMonthList.length);
			// console.log(refresh);
			// console.log(!this.showMonthList.length || refresh);
			//  这里的判断真不知到为什么 if h和 else  都调用了
			if (!this.showMonthList.length || refresh) {
				// console.log(!this.showMonthList.length || refresh);

				// console.log('if');
				const before = this.getMonthDays(beforeDate);
				const current = this.getMonthDays(currentDate);
				const after = this.getMonthDays(afterDate);
				this.showMonthList = [before, current, after];
				// return false;
			} else {
				// return false;
				// console.log('else');
				// console.log(!this.showMonthList.length || refresh);
				this.currentIndex = index;
				const [beforeIndex, currentIndex, afterIndex] = [[2, 0, 1], [0, 1, 2], [1, 2, 0]][this.currentIndex];
				const before = this.showMonthList.find(item => item.id === beforeDate) || this.getMonthDays(beforeDate);
				const current = this.showMonthList.find(item => item.id === currentDate) || this.getMonthDays(currentDate);
				const after = this.showMonthList.find(item => item.id === afterDate) || this.getMonthDays(afterDate);
				this.showMonthList[beforeIndex] = before;
				this.showMonthList[currentIndex] = current;
				this.showMonthList[afterIndex] = after;
			}
		},
		close() {
			this.active = false;
			this.$emit('close');
			this.$emit('update:visible', false);
			this.closeTimer = setTimeout(() => {
				this.visibleSync = false;
			}, 200);
		},
		animationfinish_tap(e) {
			// return false;
			console.log('animationfinish');
			const oldDate = this.currentDate;
			let index = e.detail.current;
			// console.log(index);
			this.currentIndex = index;
			this.currentDate = this.showMonthList[index].id;
			this.setShowMonthList(index);
			this.$emit('date-change', this.currentDate, oldDate);
		},
		getMonthDays(dateStr) {
			// 获取该年月的日期信息
			const [year, showMonth] = dateStr.split('/');
			const month = showMonth - 1;
			const firstDayOfMonth = new Date(year, month, 1).getDay(); //当月第一天是周几
			const lastDateOfMonth = new Date(year, month + 1, 0).getDate(); //当月最后一天是几号
			const lastDayOfLastMonth = new Date(year, month, 0).getDate(); //上一月的最后一天是周几
			const days = [];
			let line = 0;
			for (let i = 1; i <= lastDateOfMonth; i++) {
				let dow = new Date(year, month, i).getDay();
				let k;
				//第一行
				if (dow === 0) {
					days[line] = [];
				} else if (i === 1) {
					days[line] = [];
					k = lastDayOfLastMonth - firstDayOfMonth + 1;
					for (let j = 0; j < firstDayOfMonth; j++) {
						const nowYear = month === 0 ? year - 1 : year;
						const nowMonth = month === 0 ? 12 : month;
						const dayOption = this.getDayOption({
							year: nowYear,
							month: nowMonth - 1,
							day: k,
							virtual: true,
							isToday: false
						});
						days[line].push(dayOption);
						k++;
					}
				}
				//普通日期
				// 当天
				const isToday = i === new Date().getDate() && month === new Date().getMonth() && +year === new Date().getFullYear();
				const dayOption = this.getDayOption({
					year: year,
					month: month,
					day: i,
					virtual: false,
					isToday
				});
				days[line].push(dayOption);
				if (dow === 6) {
					line++; //一周最后一天加行
				} else if (i === lastDateOfMonth) {
					//最后一行
					let k = 1;
					for (dow; dow < 6; dow++) {
						const nowYear = month + 2 > 11 ? year + 1 : year;
						const nowMonth = month + 2 > 11 ? 1 : month + 2;
						const dayOption = this.getDayOption({
							year: nowYear,
							month: nowMonth - 1,
							day: k,
							virtual: true,
							isToday: false
						});
						days[line].push(dayOption);
						k++;
					}
				}
			}
			return {
				id: dateStr,
				year,
				month: +showMonth,
				days
			};
		},
		getDayOption({ year, month, day, virtual, isToday }) {
			const date = new EDate([year, month, day], 'YYYY/MM/DD');
			const format = date.format();
			const time = date.getTime();
			let isDisabled = virtual;
			if (!virtual) {
				if (typeof this.disabledDate === 'function') {
					// 没有什么 具体的作用  用了之后汇报一个 call  is  undefined 的错误
					// console.log(this.disabledDate.this);
					// if(Function.prototype.call() == undefined){
					// 		isDisabled = this.disabledDate.apply(this.$parent, date)();
					// }else {
					// 		isDisabled = this.disabledDate.call(this.$parent, date);
					// }
					
				} else {
					isDisabled = this.disabledDate.includes(format);
				}
			}
			// 获取自定义日期
			const customDate = this.getCustomDate(format);
			return {
				isDisabled, // 日期禁用
				// isSelected, // 选中日期
				year,
				month: month + 1,
				day,
				date,
				time,
				format,
				virtual, // 虚拟日期
				isToday,
				customDate
			};
		},
		getCustomDate(dateStr) {
			if (typeof this.customDate === 'function') {
				return this.customDate(dateStr);
			}
			return CustomDate[dateStr] || {};
		},
		onDayClick(day, isDisabled = undefined, virtual = undefined) {
			// console.log(this.disabled);
			this.$emit('click', day);
			// 作者源码 错误  已经修改过  下边false的地方原本是 isDisabled。 小程序点击超出这 20 天之后会 进行阻止
			// 修改为 false 之后就不会了
			// console.log(isDisabled)
			// console.log(virtual)
			// console.log( this.disabled)
			if (false || virtual || this.disabled) return;
			if (this.type === 'single') {
				// console.log(1);
				this.selectedValue = day.time;
				this.$emit('change', this.getResultValue());
				return;
			}
			if (this.type === 'multiple') {
				const value = day.time;
				// console.log(2);
				const index = this.selection.findIndex(item => item === value);
				if (index < 0) {
					this.selection.push(value);
				} else {
					this.selection.splice(index, 1);
				}
				this.$emit('change', this.getResultValue());
				return;
			}
			if (!this.selection.length) {
				// console.log(3);
				this.selection.push(day.time);
			} else if (this.selection.length === 1) {
				// console.log(4);
				const firstTime = this.selection[0];
				if (day.time !== firstTime) {
					// console.log(5);
					const method = day.time > firstTime ? 'push' : 'unshift';
					this.selection[method](day.time);
				}
			} else {
				// console.log(6);
				this.selection = [day.time];
			}
			// console.log(7);
			
			this.$emit('change', this.getResultValue());
		},
		getResultValue() {
			if (this.type === 'single') {
				return this.format && this.selectedValue ? EDate.format(this.selectedValue, this.format) : new Date(this.selectedValue);
			} else {
				return this.format ? this.selection.map(item => EDate.format(item, this.format)) : this.selection.map(item => new Date(item));
			}
		},
		doSubmit() {
			const value = this.getResultValue();
			this.$emit('input', value);
			this.$emit('submit', value);
			this.close();
		},
		refresh() {
			this.$nextTick(() => {
				// setTimeout(()=>{
					console.log('refresh');
					this.initCustomData();
					// 函数里边有错误，  调用 refs 会调用两次 然后会 闪一下
					// console.log();
					var index = 1;
					this.currentIndex = index;
					this.setShowMonthList(this.currentIndex , true);
				// },5000)
				
			});
			// console.log(a);
		},
		toYear(year) {
			console.log('toYear');
			// console.log(!this.currentDate);
			if (!this.currentDate) return;
			let oldDate = this.currentDate;
			if (year === 0) {
				const currentDate = new EDate().format('YYYY/MM');
				if (currentDate === this.currentDate) return;
				this.currentDate = currentDate;
				this.setShowMonthList(this.currentIndex);
			} else {
				// console.log(this.currentDate);
				// console.log(year);
				var yea = this.currentDate.slice(0,4)
				// console.log();
				var mom =  this.currentDate.slice(5)
				var data = yea+ '-' + mom + '-' + '01'
				if (year == -1) {
					year = 1
				}else {
					year = -1
				}
				var data = moment(data).subtract(year, 'years').format('YYYY-MM-DD');
				var arr = data.split('-')
				var data =arr[0] + '/' + arr[1] 
				// console.log(data);
				// this.currentDate = 	EDate.modify(this.currentDate, { y: year }).format('YYYY/MM');
				
				this.currentDate = data
				
				this.setShowMonthList(this.currentIndex);
				
			}
			this.$emit('date-change', this.currentDate, oldDate);
		}
	}
};
